/* generality */
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins';
  /* letter-spacing: 1px; */
}
:root{
  --header-height:50px;
  --first-color: #e70909;
  --first-color-second:#e70909;
  --first-color-alt:black;
  --first-color-lighter:rgb(109, 105, 105);
  --title-color:hsl(1, 8%, 15%);
  --text-color:hsl(0, 4%, 30%);
  --text-color-light:hsl(var(--hue-color), 8%, 65%);
  --input-color:hsl(var(--hue-color), 70%, 96%);
  --body-color:hsl(var(--hue-color), 60%, 99%);
  --container-color:white;
}
body{
  /* background-color: rgb(230, 228, 228); */
  background: #f7f7f7 ;
}
/* html{
  overflow-x: hidden;
} */
.cap{
  text-transform: capitalize;
}
p{
  color: var(--text-color);
}
p::first-letter{
  text-transform: uppercase;
}
a{
  cursor: pointer;
}
button:focus,
button{
  outline: none;
  border: none;
  cursor: pointer;
  text-transform: capitalize;
}
::-webkit-scrollbar{
  width: 8px;
}
::-webkit-scrollbar-thumb{
  background-color: var(--first-color);
  border-radius: 20px;
}
::-webkit-scrollbar-track{
  background-color: gray;
  border: 1px solid black;
  border-radius: 20px;
}
::selection{
  background-color: var(--first-color);
  color:white;
}
ul{
  padding: 0 !important;
  margin: 0 !important;
  list-style: none;
}
input:focus,
input{
  /* border: none; */
  outline: none;
}
.darkbg{
  background-color: #1A1A1A;
}
.darkbg .section_subtitle,
.darkbg p,
.darkbg h2,
.darkbg h3,
.darkbg{
color: white;
}
.section{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  padding: 50px 0;
}
.container{
  position: relative;
    width: 100%;
    padding:0 100px;
}
.flex{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.centred{
  display: flex;
  align-items: center;
  justify-content: center;
}


.logo{
  font-family: "DancingScript";
  font-size: 32px;
  text-shadow: -1px 1px 4px black;
  color: white;
}
.logo span{
  color: var(--first-color);
  font-family: "DancingScript";
}
.send-Btn,
.btn{
  padding: 10px 15px;
  background-color: var(--first-color);
  color: white;
  border-radius: 8px;
  position: relative;
  overflow: hidden;
  width: max-content;
  display: flex;
}
.btn:hover{
  border-color: var(--first-color-alt);
}
.btn-content{
  text-transform: capitalize;
  z-index: 10;
  position: relative;
  width: 100%;
  text-align: center;
}
.btn-layer{
  border-radius: 8px;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: var(--first-color-alt);
  top: 0;
  left: -100%;
  z-index: 9;
  transition: .3s;
}
.btn:hover .btn-layer{
  left: 0;
}
.goLink{
  display: flex;
align-items: center;
gap: 10px;
color: var(--title-color);
font-weight: 600;
letter-spacing: 1px;
font-size: 15px;
width: max-content;
}
.icon_arrow_right{
transition: .3s;
}
.goLink:hover .icon_arrow_right{
  transform: translateX(10px);
}
.section_title{
  font-size: 24px;
  color: var(--first-color);
  letter-spacing: 1px;
  font-weight: 600;
}
.section_subtitle{
  font-size: 14px;
  color: var(--first-color-lighter);
  font-weight: 400;
  padding-bottom: 50px;
}
.section_title,
.section_subtitle{
  text-transform: capitalize;
  text-align: center;
}
h1,h2,h3,h4,h5,h6{
  text-transform: capitalize;
}
a{
  text-decoration: none;
}
/* !generality */
/* header  */
/* scrollUp */
.scrollUp{
  position: fixed;
  right: -100%;
  bottom: calc(var(--header-height) + 16px);
  background-color: var(--first-color);
  z-index: 1000;
  transition: .4s;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: .4rem;
  padding: 0 5px;
  transition: all .6s cubic-bezier(0.68, -0.55, 0.256, 1.55);
}
html{
scroll-behavior: smooth;
}
.scrollUp.show{
  right: 16px;
}

.scrollUp__icon{
font-size: 27px !important;
color: white;
}
/* !scrollUp */
.header{
  position: absolute;
  width: 100%;
  z-index: 10000;
  transition: .3s;
}
.header.fixe{
  position: fixed;
  background-color: #111;
  box-shadow: -2px -2px 10px #000000b0;
}

.container .nav{
  display: flex;
  align-items: center;
  gap: 20px;
}

.container .nav .nav_li_con{
  display: flex;
  align-items: center;
  gap: 10px;
}

.container .nav .nav_link{
  font-size: 14px;
  position: relative;
  cursor: pointer;
  line-height: 62px ;
}


.container .nav .nav_link.active,
.container .nav .nav_link:hover{
  color: var(--first-color);
  font-weight: 600;
}

.nav_li{
  position: relative;
}
.subnav{
  position: absolute;
  background-color: white;
  padding: 5px 10px !important;
  line-height: initial;
  display: flex;
  flex-direction: column;
  width: 200px;
  box-shadow: -2px 4px 5px rgba(0, 0, 0, 0.575);
  border-top: 4px solid var(--first-color);
  top: calc(100% + 20px);
  left:-6px;
  opacity: 0;
  pointer-events: none;
  transition: top .3s;
  /* bor */
}
.subnav li{
  padding: 7.5px 5px;
  color: var(--title-color);
  transition: .3s;
  border-bottom: 1px solid lightgray;
}
.header.fixe .subnav li{
  border-bottom-color:  #312f2f;
}
.subnav li:last-child{
  border-bottom: none;
  /* padding-bottom: 0; */
}
.subnav li:hover{
  background-color: var(--first-color);
  color: white;
}
.subnav li .subnav_link{
  color: var(--title-color);
  transition: .3s;
}
.subnav li:hover .subnav_link{
  color: white;
}

.nav_li:hover .subnav{
  top: 100%;
  opacity: 1;
  pointer-events: all;
}
.nav_li .nav_arrow{
  color: var(--text-color);
  border-radius: 50%;
transition: .3s;
cursor: pointer;
}
.header.fixe .nav_li .nav_arrow{
  color: white;
}
.nav_li.nav_arrow.active,
.nav_li:hover .nav_arrow{
  color: var(--first-color);
  transform: rotate(180deg);
}

.searchForm{
  display: flex;
  align-items: center;
  gap: 4px;
  line-height: initial;
  height: 40px;
  box-shadow: -2px 4px 5px rgba(0, 0, 0, 0.575);
  padding: 2px;
  border-radius: 5px;
}
.searchForm input{
height: 100%;
border: none;
padding: 0 5px;
border-radius: 5px;
font-size: 15px;
color: black;
}

.searchForm button{
  background-color: var(--first-color);
  color: white;
  height: 100%;
  padding: 0 10px;
  border-radius: 5px;
}
.searchForm button .searchIcon{
  font-size: 25px !important;
}
.header.fixe .subnav{
  background-color: #111;
}
.header.fixe .logo,
.header.fixe li a{
  color: white;
}
.searchIcon_mobile{
  display: none;
}
.icon_menu{
  width: 25px;
  height: 16px;
  background: none;
  position: relative;
  cursor: pointer;
  overflow: hidden;
  display: none;
}
 
.icon_menu .icon_center{
width: 100%;
height: 2px;
position: absolute;
left: 0;
top: 0;
bottom: 0;
margin: auto;
transition: .3s;
}

.icon_menu.show .icon_center{
  width: 0;
}
.icon_menu .icon_center,
.icon_menu .icon_side::before,
.icon_menu .icon_side::after{
  background-color: var(--first-color);
}
.icon_menu .icon_side::before,
.icon_menu .icon_side::after{
  content: "";
  width: 100%;
  height: 2px;
  position: absolute;
  left: 0;
  transform: rotate(0);
  transition:transform .3s;
  /* margin: initial; */
  }
  
  .header.fixe .icon_menu .icon_side::before,
  .header.fixe .icon_menu .icon_side::after,
  .header.fixe .icon_menu .icon_center{
    background-color: white;
  }
  .icon_menu.show .icon_side::before,
  .icon_menu.show .icon_side::after{
    top: 0;
    bottom: 0;
    margin: auto;
  }
  .icon_menu .icon_side::before{
    top: 0;
  }
  .icon_menu.show .icon_side::before{
    transform: rotate(-45deg);
  }
  .icon_menu .icon_side::after{
    bottom: 0;
  }
  .icon_menu.show .icon_side::after{
    transform: rotate(45deg);
  }
  .icon_search_mobile{
    display: none;
  }
/* !header  */

/* banner  */
.banner{
  height: 100vh;
  width: 100%;
}
.bannerbg.blog{
  background: linear-gradient(130deg,rgba(255, 255, 255, 0.644) 50%,transparent 55%) ,url(../public/assets/bg/2.jpg) ;
  width: 100%;
  height: 100%;
  color: transparent;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
    align-items: center;
    justify-content: left;
    background-position: center;
    background-attachment: fixed;
}
.bannerbg{
  width: 100%;
  height: 100%;
  color: transparent;
  background: linear-gradient(130deg,rgba(255, 255, 255, 0.644) 50%,transparent 55%) ,url(../public/assets/core-feature.png) ;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
    align-items: center;
    justify-content: left;
    background-position: center;
    background-attachment: fixed;
}

.bannerContent .subtitle{
  color: var(--text-color);
  font-size: 18px;
  letter-spacing: 4px;
  font-weight: lighter;
}
.bannerContent .title{
  margin: 10px 0;
  display: flex;
  align-items: center;
  gap:5px;
  font-weight: 400 !important;
  color: black;
}
.bannerContent .title .word{
  color: black;
  margin-right: 5px;
}
.bannerContent .title .typewriter .word::first-letter{
  font-family: "DancingScript";
}
.bannerContent .title .logo{
  margin-right: 10px;
}
.typewriter{
  color: var(--first-color) !important;
  text-transform: capitalize;
}
.bannerDesc{
  padding-bottom: 10px;
  width: 50%;
}
/* !banner  */
/* about  */
.about_container{
  display: flex;
  gap: 6rem;
}
.about_images{
  margin-top: 2rem;
  position: relative;
  justify-self: center;
}
.about_img_big{
  width: 428px;
}
.about_img_small{
  border: 10px solid var(--first-color);
  border-top-color:white ;
  border-left-color:white ;
  border-radius: 10px;
  position: absolute;
  transform: translateX(-7rem);
  width: 153px;
  top: 0;
  bottom: 0;
  margin: auto 0;
  transition: .9s;
}
.about_text{
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.about_title{
  font-weight: 400;
  color: var(--title-color);
  position: relative;
}
.about_desc{
  border-bottom: 1px solid lightgray;
  padding-bottom: 20px;
}
.about_buttons{
  display: flex;
  align-items: center;
  gap: 30px;
}
.about_link{
  display: flex;
  align-items: center;
  gap: 10px;
  transition: .3s;
  color: var(--title-color);
  font-weight: 600;
}
.about_link .icon_arrow_right{
  transition: .3s;
}
.about_link:hover .icon_arrow_right{
transform: translateX(10px);
}
.features{
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.feature{
  width: calc(100% / 4 - 10px);
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 5px;
}
.feature .icon {
  color: var(--title-color);
  font-size: 30px !important;
}
.featuresTop{
  color: var(--first-color);
  font-weight: 600;
}
.featuresBottom{
  letter-spacing: 1px;
  font-size: 14px;
  color: var(--text-color);
}
/* !about  */
/* services */
.services_container{
  flex-wrap: wrap;
  gap: 20px;
}
.service_item{
background-color: white;
border: 1px solid lightblue;
width: calc(100% / 3 - 20px);
padding: 15px;
display: flex;
align-items: center;
justify-content: center;
flex-direction: column;
gap: 10px;
box-shadow: -2px 3px 10px rgb(0 0 0 / 47%);
text-align: center;
/* color: rgba(255,28,8,0.8);
color: rgba(255,0,82,0.8); */
transition: .3s;
}

.service_item:hover{
  background: linear-gradient(
              45deg,
              rgba(255,28,8,0.8) 0%,
              rgba(255,0,82,0.8) 100%),
              url(./shape-left.png);
  
}
.service_icon{
  background-color: var(--first-color);
  color: white;
  display: flex;
align-items: center;
justify-content: center;
border-radius: 50%;
transition: .3s;
}
.service_item:hover .service_icon{
  background-color: white;
  color: var(--first-color);
}
.service_icon .icon{
  font-size: 50px;
  padding: 10px;
}
.service_title{
  font-size: 20px;
  color: var(--title-color);
  font-weight: 400;
  transition: .3s;
}
.service_desc{
  font-size: 15px;
  transition: .3s;
}
.service_link{
  display: flex;
align-items: center;
gap: 10px;
color: var(--first-color);
font-weight: 600;
letter-spacing: 1px;
font-size: 15px;
transition: .3s;
}


.service_item:hover .service_title,
.service_item:hover .service_desc,
.service_item:hover .service_link{
  color: white;
}

.service_link:hover{
  color: var(--first-color);
}
.service_link:hover .icon_arrow_right{
  animation: scrollx infinite 1.3s ease;
}
/* !services  */
/* portfolio */
.portfolio_container{
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.portfolio_filters{
  display: flex;
  justify-content: center;
  row-gap: 10px;
  column-gap: 20px;
  flex-wrap: wrap;
}
.portfolio_filter{
  color: var(--text-color);
  border-radius: 8px;
  padding: 10px 15px;
  font-size: 15px;
  font-weight: 600;
  letter-spacing: 1px;
  transition: .3s;
  cursor: pointer;
}
.portfolio_filter:hover,
.portfolio_filter.active{
  background-color: var(--first-color);
  color: white;
}
.portfolio_content{
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}
.portfolio_card{
  border: 1px solid lightgray;
  padding: 10px;
  width: calc(100% / 3 - 20px);
  display: flex;
  flex-direction: column;
  gap: 15px;
  /* transition: all .5s; */
}
.show.portfolio_card{
  animation: animate 0.4s cubic-bezier(0.445, 0.05, 0.55, 0.95);

}
@keyframes animate{
  0%{
      transform: scale(0.5);
  }
  100%{
      transform: scale(1);
  }
}
.hide.portfolio_card{
  transform: scale(.3);
}
.portfolio_img{
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 10px;
}
.portfolio_title{
  font-weight: 600;
  font-size: 18px;
  letter-spacing: 1px;
  transition: .3s;
}

.portfolio_card:hover .portfolio_title{
  color: var(--first-color);
}
.portfolio_buttons{
  display: flex;
  justify-content: center;
  gap: 20px;
}
.tooltipIcon{
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--first-color);
  color: white;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
  position: relative;
  z-index: 2;
}
.tooltipBtn{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  z-index: 2;
}
.tooltipContent{
  opacity: 0;
  pointer-events: none;
  width: max-content;
  position: absolute;
  top: -40px;
background-color: var(--first-color);
border-radius: 25px;
color: white;
box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
padding:10px;
font-size: 14px;
text-transform: capitalize;
letter-spacing: 1px;
transition:top .3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.tooltipBtn:hover .tooltipContent{
  opacity: 1;
  pointer-events: all;
  top: -55px;
}
.tooltipContent::before{
  position: absolute;
  content: "";
  height: 15px;
  width: 15px;
  background-color: var(--first-color);
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%) rotate(45deg);
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
}
/* !portfolio */
/* pricing  */
.pricing_container{
  width: 100%;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
}
.pricing .table{
  background-color: white;
width: calc(100% / 3 - 20px);
padding: 20px;
box-shadow:  -2px 2px 5px rgba(0, 0, 0, 0.2);
position: relative;
border-radius: 8px;
transition: .3s cubic-bezier(.4,.2,.2,1);
}

.pricing .table:hover{
  box-shadow:  -2px 2px 8px rgba(0, 0, 0, 0.2);
  transform: translateY(-20px);
}

.price-section{
  display: flex;
  align-items: center;
  justify-content: center;
}
.price-section .price-area{
  height: 120px;
  width: 120px;
  background-color: red;
  border-radius: 50%;
  padding: 2px;
}
.price-section .price-area .inner-area{
height: 100%;
width: 100%;
background-color: black;
border-radius: 50%;
border: 3px solid white;
color: white;
display: flex;
align-items: center;
justify-content: center;
text-align: center;
/* position: relative; */
}
.price-area .inner-area .text{
  font-size: 25px;
  transform: translate(-5px,-10px);
  /* position: absolute; */
}
.price-area .inner-area .price{
  font-size: 45px;
  font-weight: 600;
}
.package-name{
  width: 100%;
  height: 2px;
  background-color: red;
  margin: 35px 0;
  position: relative;
}
.pricing .btn{
  width: 50%;
  text-align: center;
}
.pricing .btn .btn-content{
  text-align: center;
}
.basic .btn,
.basic .price-section .price-area,
.basic .package-name,
.basic .price-section .price-area .inner-area{
  background: linear-gradient(90deg,blue,darkmagenta);
}
.basic  .btn .btn-layer{
  background: linear-gradient(180deg,blue,darkmagenta);
}
.premium .btn,
.premium .price-section .price-area,
.premium .package-name,
.premium .price-section .price-area .inner-area{
  background-color: var(--first-color);
  background: linear-gradient(90deg,darkgreen,gold);
}
.premium   .btn .btn-layer{
  background: linear-gradient(180deg,darkgreen,gold);
}
.ulimate  .btn,
.ulimate .price-section .price-area,
.ulimate .package-name,
.ulimate .price-section .price-area .inner-area{
  background: linear-gradient(90deg,rgb(235, 15, 235),var(--first-color));
}
.ulimate  .btn .btn-layer{
  background: linear-gradient(180deg,rgb(235, 15, 235),var(--first-color));
}
.package-name::before{
  position: absolute;
  content: attr(data-target);
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  background-color: white;
  font-size: 25px;
  font-weight: 500;
  padding: 0 15px;
}
.package-features{
  margin-bottom: 20px !important;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.package-features-item{
display: flex;
justify-content: space-between;
}
.list-name{
font-size: 17px;
font-weight: lighter;
color: var(--text-color);
}
.list-icon{
  font-size: 20px !important;
}
.list-icon.check{
  color: #2DB94D;
}
.list-icon.times{
  color: #CD3241;
}
.buttonContainer{
  display: flex;
  justify-content: center;
}
.ribbon{
  height: 150px;
  width: 150px;
  position: absolute;
  top: -10px;
  left: -10px;
  overflow: hidden;
}
.ribbon::before,
.ribbon::after{
  position: absolute;
  content: "";
  border: 7px solid #aa0606;
  display: block;
  border-top-color: transparent;
  border-left-color: transparent;
  z-index: -1 !important;
}
.ribbon::before{
  top: 0;
  right: 15px;
}
.ribbon::after{
  bottom: 15px;
  left: 0px;
}
.ribbon span{
  position: absolute;
  background-color: var(--first-color);
  color: white;
  width: 200px;
  padding: 10px 0;
  top: 30px;
  right: 0;
  text-align: center;
  transform: rotate(-45deg);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}
/* !pricing  */
/* subscribe */
#subscribe{
  background: #1A1A1A;
}
#subscribe .section_subtitle,
#subscribe p{
  color: white;
}
.subscribe_container{
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.subscribe_content{
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.subscribeForm{
