/* team  */


.team_card{
  height: 300px;
  margin: 0 10px;
  transform-style: preserve-3d;
  perspective: 1000px;
}
.team_card:hover .back_face{
transform: rotateY(0);
}
.team_card:hover .front_face{
  transform: rotateY(180deg);
  }
.front_face,
.back_face{
  position: absolute;
  height: 100%;
  width: 100%;
  text-align: center;
  background:url(../public/assets/blog/2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  border-radius: 10px;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  transition:transform .7s cubic-bezier(.4,.2,.2,1);
}
.team_card .contents{
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 10px;
  transform:  translateZ(60px);
}
.team_card .front p{
  color: white;
  font-size: 25px;
  text-shadow: 1px 2px 3px rgb(0 0 0 / 88%);
  
}
.team_card .front p::after{
  content: "";
  display: block;
  width: 100px;
  height: 2px;
  background-color: var(--first-color);
  margin: 0 auto;
  margin-top: 10px;
  left: 0;
  right: 0;
}
.team_card .front span{
  color: white;
  font-size: 20px;
  text-shadow: 1px 2px 3px rgb(0 0 0 / 88%);
}
.back_face{
  transform: rotateY(180deg);
  overflow: hidden;
  background: linear-gradient(rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.7)) , url(../public/assets/blog/2.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.team_card .back{
  color: white;
  text-shadow: 1px 2px 3px rgb(0 0 0 / 88%);
}
.team_card .back h2{
  font-size: 25px;
}
.team_card .back span{
  font-size: 20px;
}
.team_card .back .icons{
  display: flex;
  gap: 10px;
}
.team_card .back .icons .icon{
  font-size: 35px !important;
  color: var(--first-color);
  background-color: white;
  border-radius: 50%;
  padding: 5px;
  cursor: pointer;
  transition: .3s;
}
.team_card .back .icons .icon:hover{
  transform: rotate(360deg);
}
