/* laptop */
@media (max-width:1200px){
  .container{
    padding: 0 50px;
  }
 
}
@media (max-width:1140px){

  .searchForm {
    position: absolute;
    top: calc(100% + 40px);
    right: 50px;
    background-color: #272626;
    opacity: 0;
    pointer-events: none;
    transition: top .3s;

  }
  .searchForm.show{
    opacity: 1;
    top: calc(100% + 20px);
    pointer-events: all;
  }
  .searchForm::before{
    position: absolute;
    content: "";
    top: -13px;
    right: 0;
    width: 0;
    height: 0;
    z-index: -1;
    margin: -14px 0 0 ;
    border: 10px solid transparent;
    border-bottom-color: #272626;
  }
  .searchForm::after{
    position: absolute;
    content: "";
    height: 50px;
    width: calc(100% + 20px);
    padding: 2px;
    background-color: #272626;
    border-radius: 5px;
    z-index: -2;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
  }
